"use strict";
import { RowBetween } from "components/deprecated/Row";
import SettingsTab from "components/Settings";
import { useAccount } from "hooks/useAccount";
import styled, { useTheme } from "lib/styled-components";
import { ArrowLeft } from "react-feather";
import { Link, useLocation } from "react-router-dom";
import { Box } from "rebass";
import { useAppDispatch } from "state/hooks";
import { resetMintState } from "state/mint/actions";
import { resetMintState as resetMintV3State } from "state/mint/v3/actions";
import { ThemedText } from "theme/components";
import { flexRowNoWrap } from "theme/styles";
import { Trans } from "uniswap/src/i18n";
const Tabs = styled.div`
  ${flexRowNoWrap};
  align-items: center;
  border-radius: 3rem;
  justify-content: space-evenly;
`;
const StyledLink = styled(Link)`
  flex: ${({ flex }) => flex ?? "none"};
  display: flex;
  align-items: center;

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToMedium`
    flex: none;
    margin-right: 10px;
  `};
`;
const FindPoolTabsText = styled(ThemedText.H1Small)`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
`;
const StyledArrowLeft = styled(ArrowLeft)`
  color: ${({ theme }) => theme.neutral1};
`;
export function FindPoolTabs({ origin }) {
  return <Tabs><RowBetween style={{ padding: "1rem 1rem 0 1rem", position: "relative" }}>
    <Link to={origin}><StyledArrowLeft /></Link>
    <FindPoolTabsText><Trans i18nKey="pool.import.v2" /></FindPoolTabsText>
  </RowBetween></Tabs>;
}
const AddRemoveTitleText = styled(ThemedText.H1Small)`
  flex: 1;
  margin: auto;
  text-align: ${({ $center }) => $center ? "center" : "start"};
`;
export function AddRemoveTabs({
  adding,
  creating,
  autoSlippage,
  positionID,
  children
}) {
  const { chainId } = useAccount();
  const theme = useTheme();
  const dispatch = useAppDispatch();
  const { state } = useLocation();
  const location = useLocation();
  const poolLink = location.pathname.includes("add/v2") ? "/pools/v2" : "/pools" + (positionID ? `/${positionID.toString()}` : "");
  const target = state?.from ?? poolLink;
  return <Tabs><RowBetween style={{ padding: "1rem 1rem 0 1rem" }} align="center">
    <StyledLink
      to={target}
      onClick={() => {
        if (adding) {
          dispatch(resetMintState());
          dispatch(resetMintV3State());
        }
      }}
      flex={children ? "1" : void 0}
    ><StyledArrowLeft stroke={theme.neutral2} /></StyledLink>
    <AddRemoveTitleText $center={!children}>{creating ? <Trans i18nKey="pool.create.pair" /> : adding ? <Trans i18nKey="common.addLiquidity" /> : <Trans i18nKey="pool.removeLiquidity" />}</AddRemoveTitleText>
    {children && <Box style={{ marginRight: ".5rem" }}>{children}</Box>}
    <SettingsTab autoSlippage={autoSlippage} chainId={chainId} hideRoutingSettings />
  </RowBetween></Tabs>;
}
